.me{
    padding-top: 10em;
    min-height: 100vh;
    z-index: 99;
}
.title_about{
    text-align: center;
    font-size: 50px;
    color:$color2;
    font-weight: bold;
}
.divider {
    display: flex;
    &:before,
    &:after {
      content: "";
      flex: 1;
    }
  }
  
  .line {
    align-items: center;
    margin: .5em 0;
    text-align: center;
    &:before,
    &:after {
    background-color: $color2;
      height: 1em;
      margin: 0 1em;
    }
  }

.about{
    // background-image: url("./../../img/BGME2.png");
    // padding-top: 10em;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 4em;
    min-height: 100vh;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    height: 100%;
    // padding: 4em 0;
    @include responsive(hd){
        margin-top: 8em;
    }
    @include responsive(hd){
        margin-top: 8em;
    }
    @include responsive(m){
        padding: 0 1em;
    }
    &_img{
        // justify-content: center;
        display: flex;
        align-items: center;
        img{
            width: 362px;
            height: 480px;
            border-radius: 1em;
            // @include responsive(m){
            // width: 562px;
            // height: 718px;
            // }
        }
    }
    &_desc{
        display: flex;
        width: 550px;
        flex-direction: column;
        justify-content: center;
        color: $color5;
        h2{
            font-size: 70px;
            font-weight: 800;
            @include responsive(l){
            font-size: 60px;
            }
            @include responsive(s){
            font-size: 40px;
            }
        }
        p{
            font-size: 17px;
            @include responsive(s){
            font-size: 17px;
            }
        }
        .skill{
            display: flex;
            gap: 3em;
        }
        ul{
            li{
                margin: 2px;
                font-size: 20px;
                list-style: none;
            }
        }
    }
}

// HISTORY

#timeline {
    // z-index: -1;
    // background-image: url("./../../img/BGME2.png");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    // margin-bottom: 5em;
    display: flex;
    // background-color: $color1;
    gap: 3em;
    justify-content: center;
    height: 100%;
    min-height: 100vh;
    &:hover {
      .tl-item {
        // width: 23.3333%;
      }
    }
  }

  .tl-item {
    transform: translate3d(0, 0, 0);
    position: relative;
    width: 13%;
    height: 50vh;
    color: #fff;
    overflow: hidden;
    transition: width 0.5s ease;
    border-radius: 20px;
    &:first-child{
        margin-top: 15em;
    }
    &:nth-child(2){
        margin-top: 10em;
    }
    &:nth-child(3){
        margin-top: 5em;
    }
    
    &:before, &:after {
      transform: translate3d(0, 0, 0);
      content: '';
      position: absolute;
      left: 0; top: 0;
      width: 100%; height: 100%;
    }
  
    &:after {
    background-color: $color2;
      opacity: .7;
      transition: opacity 0.5s ease;
    }
  
    &:before {
      background: linear-gradient(to bottom, rgba(0,0,0,0) 0%,rgba(0,0,0,1) 75%);
      z-index: 1;
      opacity: 0;
      transform: translate3d(0, 0, 0) translateY(50%);
      transition: opacity 0.5s ease, transform 0.5s ease;
    }
  
    &:hover {
      width: 20% !important;
      
      &:after {
        opacity: 0;
      }
  
      &:before {
        opacity: 1;
        transform: translate3d(0, 0, 0) translateY(0);
        transition: opacity 1s ease, transform 1s ease 0.25s;
      }
      
      .tl-content {
        opacity: 1;
        transform: translateY(0);
        transition: all 0.75s ease 0.5s;
      }
  
      .tl-bg {
        filter: grayscale(0);
      }
    }
  }
  
  .tl-content {
    transform: translate3d(0, 0, 0) translateY(25px);
    position: relative;
    z-index: 1;
    text-align: center;
    margin: 0 1.618em;
    top: 55%;
    opacity: 0;
    
    h1 {
      text-transform: uppercase;
      color: $color4;
      font-size: 1.44rem;
      font-weight: normal;
    }
  }
  
  .tl-year {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    z-index: 1;
    
    p {
      font-family: 'Pathway Gothic One',Helvetica Neue,Helvetica,Arial,sans-serif;
      font-size: 1.728rem;
    }
  }
  
  .tl-bg {
    transform: translate3d(0, 0, 0);
    position: absolute;
    width: 100%; height: 100%;
    top: 0; left: 0;
    background-size: cover;
    background-position: center center;
    transition: filter 0.5s ease;
    filter: grayscale(100%);
  }