nav{
    // margin: auto;
    // background-color: tomato;
    display: flex;
    justify-content: space-between;
    position: fixed;
    align-items: center;
    width: 90%;
    top: 0;
    margin: 1em auto;
    // padding: 1.5em;
    // margin-top: 1.5em;
    left: 0;
    right: 0;
    z-index: 99;
    @include responsive(s){
        padding: 1em;
        // width: calc(100% - 1em);
    }

    // Menu List
    .nav_menu{
        align-items: center;
        font-size: 23px;
        color: $color5;
        display: flex;
        justify-content: space-around;
        width: 30%;
        @include responsive(xl){
        width: 50%;
        }
        @include responsive(l){ 
        width: 60%;
        }
        @include responsive(m){
        width: 70%;
        }
        @include responsive(s){
        width: 85%;
        font-size: 20px;
        }

        &_list{
            list-style: none;

            // List
            &_link{
                text-decoration: none;
                color: $color6;
                &::after{
                content: '';
                display: block;
                width: 0;
                height: 7px;
                background: $color5;
                transition: all .1s ease-in;
                transform: translate(0px,-8px);
                opacity: 0;
                }
            }
        }
    }

    .nav_menu_list_link:hover::after {
        opacity: 1;
        width: 100%;
    }
    
    .active::after{
        content: '';
        display: block;
        width: 100%;
        height: 7px;
        background: $color5;
        transition: all .1s ease-in;
        transform: translate(0px,-8px);
        opacity: 1;
    }
    
}